import * as types from '../../mutation-types';

export const getDates = ({ commit }, data) => {
  commit(types.GET_DATES, data);
};

export const setCenter = ({ commit }, code) => {
  commit(types.SET_CENTER, code);
};

export const setApptInfo = ({ commit }, info) => {
  commit(types.SET_APPT_INFO, info);
};



