/* ============
 * Schedule Module Mutations
 * ============
 *

 */

import {
  GET_DATES,
  SET_CENTER,
  SET_APPT_INFO,
} from './../../mutation-types';

export default {
  [GET_DATES](state, data) {
    state.datesArray = data.data.dateTimes;
    state.centerInfo = data.data.centerInfo;
  },
  [SET_CENTER](state, code) {
    state.currentCenter = code;
  },
  [SET_APPT_INFO](state, info) {
    state.apptInfo = info;
  },
};



