/* ============
 * Routes File
 * ============
 *
 * The routes and redirects are defined in this file
 */
import ScheduleService from '@/services/schedule';
/**
 * The routes
 *
 * @type {object} The routes
 */
export default [
  // default
  {
    path: '/schedule/:centerCode/:date?',
    name: 'schedule',
    component: require('./pages/index/index.vue'),
    beforeEnter: (to, from, next) => {
      ScheduleService.getDates(to.params.centerCode);
      next();
    },
  },
  // enter details
  {
    path: '/details/:centerCode/:date/:time',
    name: 'details',
    component: require('./pages/index/index.vue'),
    beforeEnter: (to, from, next) => {
      ScheduleService.getDates(to.params.centerCode);
      next();
    },
  },
  // appointment Info
  {
    path: '/apptInfo',
    name: 'apptInfo',
    component: require('./pages/apptInfo/apptInfo.vue'),
    // beforeEnter: (to, from, next) => {
    //   next();
    // },
  },


  {
    path: '/',
    redirect: '/schedule/Albany',
  },
  {
    path: '/*',
    redirect: '/schedule/Albany',
  },
];



