/* ============
 * Ischedule default Page
 * ============
 *
 */

import ScheduleService from '@/services/schedule';
import MaskedInput from 'vue-masked-input';
import moment from  'moment';

export default {
  name: 'Default-page',
  data() {
    return {
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      monthOffset: 0,
      dateFromRoute: false,
      chosenDate: null,
      chosenTime: null,
      availableTimes: [],
      userInfo: {
        fname: null,
        lname: null,
        areaCode: null,
        phoneNo: null,
        email: null,
        dob: null,
        phone: null,
        phoneRaw: null,
        smsOptin: 'Y',
        dummyResponse: 1,
      },
      validationErrors: {
        dob: 'Please, enter correct date',
      },
      wasInputData: {

      },
    };
  },
  components: {
    MaskedInput,
  },
  computed: {
    centerInfo() {
      return this.$store.state.schedule.centerInfo;
    },
    currentCenter() {
      return this.$store.state.schedule.currentCenter;
    },
    chosenFullDate() {
      var date = moment(this.chosenDate);
      return date.format('dddd, MMMM D');
    },
    chosenShortDate() {
      var date = moment(this.chosenDate);
      return date.format('MMM D');
    },
    chosenFullDateTop() {
      var date = moment(this.chosenDate);
      return date.format('MMMM Do, YYYY');
    },
    availableDates() {
      const dates = {};
      for (let d in this.$store.state.schedule.datesArray) {
        dates[this.$store.state.schedule.datesArray[d].date] = this.$store.state.schedule.datesArray[d].times;
      }
      return dates;
    },
    nowDate() {
      const now = new Date();
      if (this.monthOffset !== 0) {
        const month = now.getMonth() + this.monthOffset;
        now.setMonth(month);
      }
      return now;
    },
    thisYear() {
      return this.nowDate.getFullYear();
    },
    thisMonthName() {
      return this.months[this.nowDate.getMonth()];
    },
    firstDayThisMonthWeekDay() {
      this.nowDate.setDate(1);
      return this.nowDate.getDay();
    },
    lastDayThisMonth() {
      this.nowDate.setMonth(this.nowDate.getMonth()+1);
      this.nowDate.setDate(0);
      return this.nowDate.getDate();
    },
    nextMonthAvailable() {
      for (const index in this.availableDates) {
        if (this.nowDate.getMonth()+1 < parseInt(index.split('/')[0], 10)) {
          return true;
        }
      }
      return  false;
    },
    thisMonthAvailable() {
      for (const index in this.availableDates) {
        if (this.nowDate.getMonth() === parseInt(index.split('/')[0], 10)) {
          return true;
        }
      }
      return  false;
    },
    prevMonthAvailable() {
      for (const index in this.availableDates) {
        if (this.nowDate.getMonth()+1 > parseInt(index.split('/')[0], 10)) {
          return true;
        }
      }
      return  false;
    }
  },

  methods: {
    isChosenDate(day) {
      const date = this.nowDate;
      date.setDate(day);
      const momentDate = moment(date);
      const index = momentDate.format('M/D/YYYY');
      return index === this.chosenDate;
    },
    moveNextMonth() {
      this.chosenDate = null;
      this.chosenTime = null;
      this.monthOffset += 1;
    },
    movePrevMonth() {
      this.chosenDate = null;
      this.chosenTime = null;
      this.monthOffset -= 1;
    },
    isDateAvailable(day) {
      const date = this.nowDate;
      date.setDate(day);
      const momentDate = moment(date);
      const index = momentDate.format('M/D/YYYY')
      if (this.availableDates.hasOwnProperty(index)) {
        return true;
      }
      return false;
    },
    chooseDate(day) {
      this.chosenTime = null;
      const date = this.nowDate;
      date.setDate(day);
      const momentDate = moment(date);
      this.chosenDate = momentDate.format('M/D/YYYY');
      this.initDate();
      this.$router.push({name: 'schedule', params:{date:this.chosenDate}});
    },
    initDate() {
      this.availableTimes = this.availableDates[this.chosenDate];
    },
    chooseTime(time) {
      this.chosenTime = time;
    },
    onSwipeLeft() {
      if (this.nextMonthAvailable) {
        this.moveNextMonth();
      }
    },
    onSwipeRight() {
      if (this.prevMonthAvailable) {
        this.movePrevMonth();
      }
    },
    goToDetails() {
      this.$router.push({ name: 'details', params: {centerCode:this.currentCenter, date: this.chosenDate, time: this.chosenTime}});
    },

    initDateFromRoute() {
      this.dateFromRoute = true;
      if (this.$route.params.date) {
        this.chosenDate = this.$route.params.date;
        this.initDate();
      }
      if (this.$route.params.time) {
        this.chosenTime = this.$route.params.time;
      }
    },

    isCorrect(index) {
      if (this.userInfo[index]) {
        switch (index) {
          case 'fname':
          case 'lname':
            if (this.userInfo[index] !== '') {
              return true;
            }
            break;
          case 'phone':
            if (this.wasInput('phone') && this.userInfo.phoneRaw.indexOf('_')  === -1 ) {
              return true;
            }
            break;
          case 'email':
            if (this.wasInput('email')) {
              const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
              return re.test(this.userInfo.email);
            }
            break;

          case 'dob':
            if (this.wasInput('dob')) {
              const date = moment(this.userInfo.dob);
              if (date.isValid()) {
                if (moment().diff(date, 'months') < 210) {
                  this.validationErrors.dob = 'Must be at least 17.5 years old';
                  return false;
                }
                return true;
              } else {
                this.validationErrors.dob = 'Please, enter correct date';
              }
            }
            break;
        }
      }
      return false;
    },

    hasError(index) {
      if (this.userInfo.hasOwnProperty(index) && this.userInfo[index] !== null) {
        switch (index) {
          case 'fname':
          case 'lname':
            if (this.userInfo[index] === '') {
              return true;
            }
            break;
          case 'phone':
            if (this.wasInput('phone') && this.userInfo.phoneRaw.indexOf('_') >-1 ) {
              return true;
            }
            break;
          case 'email':
            if (this.wasInput('email')) {
              const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
              return !re.test(this.userInfo.email);
            }
            break;

          case 'dob':
            if (this.wasInput('dob')) {
              const date = moment(this.userInfo.dob);
              if (date.isValid()) {
                if (moment().diff(date, 'months') < 210) {
                  return true;
                }
              } else {
                return true;
              }
            }
            break;

        }
      }
      return false;
    },

    watchInputData(index) {
      if (this.userInfo.hasOwnProperty(index) && this.userInfo[index]) {
        this.wasInputData[index] = true;
      }
    },
    wasInput(index) {
      return this.wasInputData.hasOwnProperty(index);
    },

    formIsValid() {
      return this.isCorrect('fname')
        && this.isCorrect('lname')
        && this.isCorrect('email')
        && this.isCorrect('phone')
        && this.isCorrect('dob');
    },

    submitForm() {
      if (this.formIsValid()) {
        this.userInfo.centerCode = this.currentCenter;
        this.userInfo.apptDate = this.chosenDate;
        this.userInfo.apptTime = this.chosenTime;
        this.userInfo.areaCode = this.userInfo.phoneRaw.toString().substring(0,3);
        this.userInfo.phoneNo = this.userInfo.phoneRaw.toString().substring(3);
        ScheduleService.sendForm(this.userInfo).then((response) => {
          console.log(response.data);
          if (response.data.hasOwnProperty('data') && response.data.data.hasOwnProperty('appointmentInfo')) {
            //set state
            this.$store.dispatch('setApptInfo', response.data.data.appointmentInfo);
            this.$router.push({name: 'apptInfo'});
          } else if (response.data.hasOwnProperty('validationErrors')) {
            let error = '';
            for (let i = 0; i < response.data.validationErrors.length; i += 1) {
              error += response.data.validationErrors[i].errorMessage+  "\n";
            }
            alert(error);
          }
        })
          .catch((error) => {
            console.log(error);
          });
      }
    }
  },

  watch: {
    centerInfo(val) {
      if (val) {
       if (!this.thisMonthAvailable && this.nextMonthAvailable) {
         this.monthOffset += 1;
       }
      }
    }
  },
  mounted() {
    this.initDateFromRoute();
  },
};



