import Vue from 'vue';
import store from './../../store';

const success = (result) => {
  store.dispatch('getDates', result.data);
};

const failed = (result) => {
  store.dispatch('commonError', result);
};

export default (centerCode) => {
  if (store.state.schedule.currentCenter !== centerCode) {
    store.dispatch('setCenter', centerCode);
    Vue.$http.get('times4Center.lp?centerCode=' + centerCode)
      .then((response) => {
        success(response);
      })
      .catch((error) => {
        failed(error);
      });
  }
};



