/* ============
 * Appointment Info Page
 * ============
 *
 */
import moment from  'moment';

export default {
  name: 'ApptInfo-page',
  data() {
    return {
    };
  },
  computed: {
    centerInfo() {
      return this.$store.state.schedule.centerInfo;
    },
    currentCenter() {
      return this.$store.state.schedule.currentCenter;
    },
    apptInfo() {
      return this.$store.state.schedule.apptInfo;
    },
    apptFullDate() {
      var date = moment(this.apptInfo.apptDate);
      return date.format('dddd, MMMM D');
    },
  },

  methods: {
  },

  mounted() {
    if (!this.apptInfo) {
      this.$router.push({name: 'schedule'});
    }
  },
};



